// takes a possibly negative number and turns it into a valid index from 0 to length-1
// example: pos=-2 length=9 returns=6
export function positionToIndex(pos, length) {
    if (Math.abs(pos) > length) {
        throw new Error("Position ".concat(pos, " out of range +/-").concat(length));
    }
    if (pos === 0) {
        throw new Error("Positions start from 1");
    }
    return pos < 0 ? this.value.length + pos : pos - 1;
}
