export var FIRST_ADDRESS = 0;
export var LAST_ADDRESS = 254;
export var WORD_SIZE = 2;
export function addressToIndex(address) {
    if (!isValidAddress(address)) {
        throw new Error("Invalid address: " + address);
    }
    return address / WORD_SIZE;
}
export function indexToAddress(index) {
    var address = index * WORD_SIZE;
    if (!isValidAddress(address)) {
        throw new Error("Invalid index: " + index);
    }
    return address;
}
export function isValidAddress(address) {
    return address % WORD_SIZE === 0 && address >= FIRST_ADDRESS && address <= LAST_ADDRESS;
}
