import { positionToIndex } from "./stringUtil";
export var MAX_BITS = 32; // js limit for bitwise operations
export var MIN_BITS = 1;
export function checkValidBitCount(bits) {
    if (bits < MIN_BITS || bits > MAX_BITS) {
        throw new Error("Binary numbers must be between ".concat(MIN_BITS, " and ").concat(MAX_BITS, " bits"));
    }
}
export function isValidBinary(bin, bits) {
    checkValidBitCount(bits);
    return new RegExp("^[01]{1,".concat(bits, "}$")).test(bin);
}
// returns wether or not a value is a valid "bits"-bit value (signed or unsigned)
export function valueIsInRange(value, bits) {
    return value <= maxValue(bits) && value >= minValue(bits);
}
export function isSigned(value, bits) {
    return value <= maxSignedValue(bits) && value >= minValue(bits);
}
export function isUnsigned(value, bits) {
    return value <= maxValue(bits) && value >= 0;
}
export function maxValue(bits) {
    checkValidBitCount(bits);
    return Math.pow(2, bits);
}
export function maxSignedValue(bits) {
    checkValidBitCount(bits);
    return Math.pow(2, bits - 1) - 1;
}
export function minValue(bits) {
    checkValidBitCount(bits);
    return -Math.pow(2, bits - 1);
}
export function valueToBinary(value, bits) {
    checkValidBitCount(bits);
    return pad((value >>> 0).toString(2), bits);
}
export function pad(bin, bits) {
    return ("00000000000000000000000000000000" + bin).slice(-bits);
}
// pos can be either negative or positive, a negative pos start from the lsb
// pos goes from 1 to binString.length or from -1 to -binString.length
export function setBit(binString, pos, value) {
    if (!/^[10]*$/.test(binString)) {
        throw new Error("Invalid binary string");
    }
    var bits = binString.split("");
    bits[positionToIndex(pos, binString.length)] = value ? "1" : "0";
    return bits.join("");
}
